@import url('https://fonts.googleapis.com/css2?family=Dosis:wght@300;400;500;600&display=swap');

* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

/*defining some root variables*/
:root {
    --color-bg: #ffffff ; /*dfdbd8*/
    --color-bg-variant: #9c7e71 ;
    --color-primary: #3b5480 ;  /*1b6c58*/
    --color-primary-variant: #a5bfcdb1 ; /*789b9293, */
    --color-accent: #77432c ;
    --color-white: #ffffff ;
    --color-light: #dedcde ; 

    --transition: all 400ms ease;

    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-sm: 90%
}
 
html {
    scroll-behavior: smooth;
}

::-webkit-scrollbar {
    display: none;
}

body {
    font-family: 'Dosis', sans-serif;
    background: var(--color-bg);
    color: var(--color-primary);
    line-height: 1.7;
     /* background-image: url(../src/assets/bg-texture.png); */
}

.container {
    width: var(--container-width-lg);
    margin: 0 auto;
}

h1, h2, h3, h4, h5 {
    font-weight: 500;
}

h1 {
    font-size: 2.5rem;
}

section {
    margin-top: 8rem;;
}

/* defining the styles for h2s and h5s in each sections  */
section > h2, section > h5 {
    text-align: center;
    color: var(--color-accent);
}

section > h2 {
    color: var(--color-primary);
    margin-bottom: 3rem;
}

.text-light {
    color:var(--color-accent);
}

a {
    color: var(--color-accent);
    transition: var(--transition);
}

a:hover {
    color: var(--color-primary-variant);
}

.btn {
    width: max-content;
    display: inline-block;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    cursor: pointer;
    border-radius: 0.4rem;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
}

.btn:hover {
    background-color: var(--color-white);
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary {
    background: var(--color-primary);
    color: var(--color-bg);
}

img  {
    display: block;
    width: 100%;
    object-fit: cover;
}

/* media queries for medium devices -> ex: tablets */
@media screen and (max-width: 1024px) {
    .container {
        width: var(--container-width-md);
    }

    section {
        margin-top: 6rem;
    }
}

/* media queries for small devices -> ex: phones */
@media screen and (max-width: 600) {
    .container {
        width: var(--container-width-sm);
    }

    section >  h2{
        margin-bottom: 2rem;
    }
}